a {
  text-decoration: none;
}

.calendar-section {
  background-color: #fff;
  width: 90%;
  max-width: 90%;
  margin: auto;
  padding: 1rem;
}

.calendar-wrapper ul {
  list-style: none;
}

.calendar-wrapper .container {
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 600px;
  height: 100%;
  display: flex;
}

.calendar {
  perspective: 1000px;
  height: 501px;
  transform-style: preserve-3d;
  background: #ff5733;
  border-radius: 4px;
  width: 100%;
  transition: all .9s;
  box-shadow: 0 5px 20px #0000004d;
}

.front {
  transform: rotateY(0);
}

.current-date {
  border-bottom: 1px solid #fff;
  padding: 30px 40px;
}

.current-date h1 {
  color: #dfebed;
  font-size: 1.4em;
  font-weight: 300;
}

.week-days {
  color: #dfebed;
  gap: 2rem;
  padding: 30px 40px;
  font-weight: 600;
  display: flex;
}

.days {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.weeks {
  color: #fff;
  flex-direction: row;
  gap: 2rem;
  display: flex;
  padding-top: .75rem !important;
  padding-bottom: .75rem !important;
}

.weeks li {
  background-color: #0000;
  border: none;
}

.btn-weekday {
  border-radius: unset;
  color: #fff;
  border: none;
  width: 100%;
  background-color: #0000 !important;
}

.btn-weekday:hover {
  font-weight: bold;
}

.weeks div {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 20px;
  font-size: 1.2em;
  font-weight: 300;
  display: flex;
}

.last-month {
  opacity: .3;
}

.weeks li {
  text-align: center;
  width: 14.2857%;
}

.weeks span.active {
  background: #f78536;
  border-radius: 50%;
}

.weeks span:not(.last-month):hover {
  cursor: pointer;
  font-weight: 600;
}

.event {
  position: relative;
}

.event:after {
  content: "•";
  color: #23f523;
  font-size: 1.4em;
  position: absolute;
  top: -4px;
  right: -4px;
}

.no-event {
  background-color: #f8dfd8;
  border-left: 3px solid #ff5733;
}

.back {
  height: 100%;
  transform: rotateY(180deg);
}

.back select {
  color: #fff;
  background: none;
  border: none;
  border-bottom: 1px solid #fff;
  width: 100%;
  padding: 30px 40px;
  font-size: 1.4em;
  font-weight: 300;
}

.back select option {
  color: #fff;
  background-color: #ff5733;
}

.info {
  color: #fff;
  flex-direction: column;
  gap: 4rem;
  width: 90%;
  margin: auto;
  font-size: 1.2em;
  font-weight: 600;
  display: flex;
}

.info span {
  font-weight: 300;
}

.input-group-addon {
  cursor: pointer;
  border-radius: 5px;
}

.info .address p {
  width: 100%;
}

.actions {
  border-top: 1px solid #fff;
  justify-content: space-between;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
}

.actions button {
  color: #fff;
  letter-spacing: 3px;
  text-transform: uppercase;
  background: none;
  border: 0;
  width: 50%;
  margin: 0;
  padding: 30px 0;
  font-weight: 600;
}

.actions button:first-of-type {
  border-right: 1px solid #fff;
}

.actions button:hover {
  cursor: pointer;
  color: #ff5733;
  background: #fff;
}

.actions button:active {
  color: #ff5733;
  background: #fff;
  outline: none;
}

.flip {
  transform: rotateY(180deg);
}

.front, .back {
  backface-visibility: hidden;
}

.feedback-container {
  border-radius: 5px;
}
